.sidebar {
    display: none;
}

.sidebar.open {
    display: flex;
    flex-direction: column;
    position: absolute;
    min-width: 317px;
    top:0px;
    left: 0px;
    min-height: 100vh;
    flex-direction: column;

    background: #FFFFFF;
    box-shadow: 0px 10px 40px rgba(29, 33, 38, 0.13), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 0px;
    z-index: 11;
}

.background {
    position: absolute;
    min-width: 317px;
    left:317px;
    top:0px;
    right: 0px;
    height: 100vh;
    background-color: black;
    opacity: 0.5;
    z-index: 11;
}

.nav {
    display: flex;
    flex-direction: column;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
    margin-left: 70px;
}

.nav a {
    margin-bottom: 20px;
    text-decoration-line: none;
}
.nav a:visited {
    color:black;
}

.nav a:hover {
    color: #58ACFF;
}

.nav a.active {
}
.close {
    align-self: flex-end;
    background: none;
    border: none;
    padding: 2px;
    margin-top: 16px;
    margin-right: 16px;
}

.close img {


}

.active {
    /* identical to box height */

    text-decoration-line: none!important;

    color: #0059B2!important;
}
